<template>
  <div>
    <v-header :isOther="true" />
    <div class="web">
      <p class="check-tip"></p>
      <RadioGroup v-model="addCheckIndex" vertical v-if="addList.length" class="address-box">
        <Radio v-for="(item, index) in addList" :value="index" :key="index" :label="index">
          <span>{{item.addName}}-{{item.addDetail}}</span>
          <span>{{item.userName}}</span>
          <span class="grayColor">{{item.userPhone}}</span>
          <span v-if="item.addState" class="grayColor"></span>
        </Radio>
        <div class="address-btn">
          <Button type="primary" class="mr8" @click="isAdd=true"></Button>
          <Button type="warning" ghost></Button>
        </div>
      </RadioGroup>
      <p class="check-tip check-order"></p>
      <div class="order-box">
        <Row class="goods-header">
          <Col span="8"></Col>
          <Col span="4"></Col>
          <Col span="4"></Col>
          <Col span="4"></Col>
          <Col span="4"></Col>
        </Row>
        <Row
          class="goods-list"
          v-for="(item, index) in  orderGoods"
          :key="index"
          v-if="item.isCheck"
        >
          <Col span="4">
            <img :src="item.goodsImg" alt />
          </Col>
          <Col span="4">{{item.goodsName}}</Col>
          <Col span="4">{{item.unitPrice}}</Col>
          <Col span="4">{{item.num}}</Col>
          <Col span="4">--</Col>
          <Col span="4" class="fw">{{item.price}}</Col>
        </Row>
      </div>
      <Row class="mb10">
        <Col span="2"></Col>
        <Col span="4">
          <RadioGroup v-model="playWay" type="button">
            <Radio label="0"></Radio>
            <Radio label="1"></Radio>
          </RadioGroup>
        </Col>
        <Col span="2"></Col>
        <Col span="6">
          <Input v-model="postscripts" placeholder="" />
        </Col>
      </Row>
      <Row class="order-content">
        <Col span="12">
          <span class="mr8"></span>
          <span class="mr8">:{{totalGoods}}</span>
          <span>:{{totalNum}}</span>
        </Col>
        <Col span="12" class="right">
          <p>
            
            <span class="fw">{{totalMoney}}</span>
          </p>
          <p>
            
            <span class="fw">20</span>
          </p>
        </Col>
      </Row>
      <Row class="play-box">
        <Col span="22">
          <span class="mr8">
            :
            <span class="redColor fw">{{totalMoney+20}}</span>
          </span>
        </Col>
        <Col span="2">
          <Button type="primary" @click="play"></Button>
        </Col>
      </Row>
    </div>
    <v-footer />
    <Modal
      v-model="isAdd"
      title=""
      :mask-closable="false"
      :footer-hide="true"
    >
      <Form ref="addForm" :model="addForm" :rules="addRule" :label-width="100">
        <FormItem label="" prop="userName">
          <Input type="text" :maxlength="25" v-model="addForm.userName" placeholder="25" />
        </FormItem>
        <FormItem label="" prop="addName">
          <Cascader
            :data="provinceList"
            v-model="addForm.addName"
            :render-format="label => label.join('-')"
          ></Cascader>
        </FormItem>
        <FormItem label="" prop="addDetail">
          <Input
            v-model="addForm.addDetail"
            type="textarea"
            :autosize="{minRows: 2,maxRows: 5}"
            placeholder=""
          />
        </FormItem>
        <FormItem label="" prop="userPhone">
          <Input v-model="addForm.userPhone" placeholder="" />
        </FormItem>
        <FormItem>
          <Checkbox v-model="addForm.addState"></Checkbox>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="addAddress" class="mr8"></Button>
          <Button @click="reset"></Button>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>
<script>
import * as validate from "@/utils/verification";
import provinceList from "@/utils/provinces.json";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      addList: [],
      addCheckIndex: 0,
      isAdd: false,
      provinceList: provinceList.data,
      addForm: {
        userName: "",
        addName: [],
        addDetail: "",
        userPhone: "",
        addState: false
      },
      addRule: {
        userName: [
          { required: true, message: "", trigger: "blur" }
        ],
        addName: [
          { required: true, validator: this.validateAddName, trigger: "change" }
        ],
        addDetail: [
          { required: true, message: "", trigger: "blur" }
        ],
        userPhone: [
          { required: true, validator: validate.phone, trigger: "blur" }
        ]
      },
      orderGoods: [], //
      postscripts: "", //
      playWay: "",
      totalGoods: 0, //
      totalMoney: 0, //
      totalNum: 0 //
    };
  },
  computed: {
    ...mapGetters(["getShopingCart"])
  },
  created() {
    this.init();
    this.getAddress();
  },
  methods: {
    // 
    init() {
      this.totalGoods = 0;
      this.totalMoney = 0;
      this.totalNum = 0;
      this.orderGoods = this.getShopingCart.filter(item => {
        if (item.isCheck) {
          this.totalGoods += 1;
          this.totalMoney += item.price * 1;
          this.totalNum += item.num;
          return item;
        }
      });
    },
    // 
    getAddress() {
      this.$api
        .addressList()
        .then(res => {
          this.addList = res.data.data || [];
          this.addList.filter((item, index) => {
            if (item.addState) {
              this.addCheckIndex = index;
            }
          });
          this.$forceUpdate();
        })
        .catch(err => console.log(err));
    },
    // 
    validateAddName(rule, value, callback) {
      if (!value.length) {
        callback(new Error(""));
      } else {
        callback();
      }
    },
    // 
    addAddress() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          let newAddress = JSON.parse(JSON.stringify(this.addForm));
          newAddress.addName = newAddress.addName.join("-");
          newAddress.addState = newAddress.addState ? 1 : 0;
          this.$api
            .addAddress(newAddress)
            .then(res => {
              this.getAddress();
              this.isAdd = false;
              this.$Notice.info({ title: "" });
            })
            .catch(err => console.log(err));
        }
      });
    },
    // 
    reset() {
      this.$refs.addForm.resetFields();
    },
    // 
    play() {}
  }
};
</script>
<style lang="less" scoped>
@import "~@/less/theme.less";
.check-tip {
  font-size: 15px;
  &.check-order {
    padding-left: 20px;
    line-height: 40px;
  }
}
.address-box {
  margin: 10px 0;
  span {
    font-size: 20px;
    margin-right: 10px;
    vertical-align: middle;
  }
  .ivu-radio-group-item {
    margin-bottom: 10px;
  }
  .ivu-radio-wrapper-checked {
    span {
      font-weight: 700;
    }
  }
}
.address-btn {
  margin: 20px 0;
}
.order-box {
  padding: 35px 10% 15px;
  border-top: 1px solid @borderColor;
  border-bottom: 1px solid @borderColor;
  margin-bottom: 20px;
}
.goods-header {
  text-align: center;
  border: 1px solid @borderColor;
  background-color: @grayBg;
  line-height: 42px;
}
.goods-list {
  margin: 20px 0;
  text-align: center;
  padding: 10px 0;
  background-color: @grayBg;
  border: 1px solid @borderColor;
  img {
    display: block;
    width: 60px;
    height: 60px;
  }
}
.order-content {
  border: 1px solid @themeColor;
  background-color: #fff5f3;
  padding: 20px;
  margin: 20px 0;
  .right {
    text-align: right;
  }
}
.play-box {
  text-align: right;
  margin-bottom: 30px;
}
</style>



